export default {
	data() {
		return {
			globalinfo:{}
		}
	},
	methods: {
		touxiang() {
			let userinfo= uni.getStorageSync("globalinfo");
			uni.showActionSheet({
				itemList:["预览头像","从相册选择上传"],
				success: (res) => {
					if(res.tapIndex == 0){
						let array=[];
						array.push(userinfo.faceImage);
						uni.previewImage({
							current:array[0],
							urls:array
						})
					}else if(res.tapIndex == 1){
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType:["album"],
							success:(res) =>{
								let tempFilePaths =res.tempFilePaths;

								uni.navigateTo({
									url:"../meface/meface?tempFilePaths="+tempFilePaths[0]
								})
								
								
							}
							
						})
						
					}
				}
			})
		},
		myclear() {
			uni.clearStorageSync();
			uni.showToast({
				title:"清理缓存成功"
			})
		},
		logout() {
			let userinfo= uni.getStorageSync("globalinfo");
			let myid = userinfo.id;
			uni.request({
				url:"https://www.imovietrailer.com/superhero/user/logout?userId=" + myid + "&qq=3129996800",
				method:"POST",
				success: (res) => {
					//console.log(res.data.status)
					if(res.data.status == 200){
						uni.removeStorageSync("globalinfo");
						uni.switchTab({
							url:"../me/me"
						})							
					}
				}
			})

		},
		gainame(){
			uni.navigateTo({
				url:"../gainame/gainame"
			})
		},
		tobirthday() {
			uni.navigateTo({
				url:"../tobirthday/tobirthday"
			})
		},
		tosex() {
			uni.navigateTo({
				url:"../tosex/tosex"
			})
		}
	},
	onShow() {
		let userinfo= uni.getStorageSync("globalinfo");
		if(userinfo){
			this.globalinfo = userinfo;
		}else{
			this.globalinfo ={};
		}

	},		
}
